import axios from 'axios'
import {message} from 'ant-design-vue'
import router from '../router'

const Message = message

let commonUrl = process.env.NODE_ENV === 'development'? '/proxy' : '';

// post
export function postRequest(url, data, config={}) {
    if(config.headers){
        config.headers.token= sessionStorage.getItem('token') || null
    }else{
        config.headers = {
            token : sessionStorage.getItem('token') || null
        } 
    }
    return  new Promise( (resolve, reject) => {
        axios({
            url: commonUrl + url,
            method: 'post',
            data: data,
            ...config
        })
        .then(res => {
            const {data:{code, message, data={}} } = res
            if(code === 1)resolve(data)
            else if(code === 2){   //token
                sessionStorage.setItem('userId', '')
                router.push({ name: 'index'})
                Message.error(message)
                reject(message)
            }else{
               console.log(message, res) 
               Message.error(message)
               reject(message)
            }
        })
        .catch(error => {
            reject('post')
        })
    })
}

// get
export function getRequest(url, data) {
    return (
            axios({
                url: commonUrl + url,
                method: 'get',
                params: data,
                //headers: headers()
            })
            .then(res => {
                return res.data
            })
            .catch(error => {
                return error
            })
    )
}

export let baseUrl = process.env.BASE_URL
